.modal-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    overflow: scroll;
}

.modal {
    padding: 30px;
    max-width: 580px;
    margin: 40px auto;
    background-color: #fff;
    border-radius: 10px;
}

.modal h2 {
    border-bottom: 1px solid #ddd;
    padding-bottom: 10px;
}
